import React from 'react';
import { Link } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import IntlMessages from '../../util/IntlMessages';
import CircularProgress from '@material-ui/core/CircularProgress';

type Props = {
    showAuthLoader: any, userSignIn: any,
    showMessage: any, loader: any, alertMessage: any
};

type State = {
    email: string, password: string
};

export default class SignInView extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        
        this.state = {
            email: 'demo@example.com',
            password: 'demo#123'
        }
    }

    render() {
        const { showMessage, loader, alertMessage } = this.props;
        const { email, password } = this.state;
        
        return (
            <div id="loginDiv" className="app-login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">

                <div className="app-login-main-content">

                    <div className="app-logo-content d-flex align-items-center justify-content-center">
                        <Link className="logo-lg" to="/" title="Prefeitura Municipal de Salvador">
                            <img src={require("assets/images/logo_pcb.png")} style={{ width: "100%" }} alt="Prefeitura Municipal de Salvador" title="Prefeitura Municipal de Salvador" />
                        </Link>
                    </div>

                    <div className="app-login-content">
                        <div className="app-login-header mb-4">
                            <h1>Senhador</h1>
                        </div>

                        <div className="app-login-form">
                            <form>
                                <fieldset>
                                    <TextField
                                        label={<IntlMessages id="appModule.email" />}
                                        fullWidth
                                        onChange={(event) => this.setState({ email: event.target.value })}
                                        defaultValue={email}
                                        margin="normal"
                                        className="mt-1 my-sm-3"
                                    />
                                    <TextField
                                        type="password"
                                        label={<IntlMessages id="appModule.password" />}
                                        fullWidth
                                        onChange={(event) => this.setState({ password: event.target.value })}
                                        defaultValue={password}
                                        margin="normal"
                                        className="mt-1 my-sm-3"
                                    />

                                    <div className="mb-3 d-flex align-items-center justify-content-between">
                                        <Button onClick={() => {
                                            this.props.showAuthLoader();
                                            this.props.userSignIn({ email, password });
                                        }} variant="contained" fullWidth color="primary">
                                            <IntlMessages id="appModule.signIn" />
                                        </Button>
                                    </div>

                                </fieldset>
                            </form>
                        </div>
                    </div>

                </div>
                {
                    loader &&
                    <div className="loader-view"> <CircularProgress /> </div>
                }
                {showMessage && NotificationManager.error(alertMessage)}
                <NotificationContainer />
            </div>
        );
    }
}