import React, { Component, ReactNode } from 'react';
import { Breadcrumb, BreadcrumbItem } from 'reactstrap';
import IconButton from '@material-ui/core/IconButton';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import "./styles.css";

class TitleBar extends Component<any, any> {

    constructor(props: any) {
        super(props);
    }

    private getDisplayString(sub: any): string {
        const arr = sub.split("-");
        if (arr.length > 1) {
            return arr[0].charAt(0).toUpperCase() + arr[0].slice(1) + " " + arr[1].charAt(0).toUpperCase() + arr[1].slice(1)
        } else {
            return sub.charAt(0).toUpperCase() + sub.slice(1)
        }
    
    }
    private getUrlString(path: string, sub: any, index: number): string {
        if (index === 0) {
            return '#/';
        } else {
            return '#/' + path.split(sub)[0] + sub;
        }
    };

    public render(): ReactNode {
        const { match } = this.props;

        const path: string = match.path.substr(1);
        const subPath: string[] = path.split('/');
        const title: string = subPath[1];

        return (
            <div className="page-heading">
                <h2 className="title mb-3 mb-sm-0">{title}</h2>

                {/* <Breadcrumb className="mb-0" tag="nav">
                    {subPath.map((sub, index) => {
                        return <BreadcrumbItem active={subPath.length === index + 1}
                            tag={subPath.length === index + 1 ? "span" : "a"} key={index}
                            href={this.getUrlString(path, sub, index)}>{this.getDisplayString(sub)}
                        </BreadcrumbItem>
                    })}
                </Breadcrumb> */}
            </div>
        );
    }
}

const mapStateToProps = (params: any) => {
    const path = params.router.location.pathname;
    return { match: { path } };
};

export default withRouter(connect(mapStateToProps)(TitleBar));