import React from 'react';
import { connect } from 'react-redux';
import { hideMessage, showAuthLoader, userSignIn } from '../../actions/Auth';

import SignInView from './view';

class SignIn extends React.Component<any, any> {
    constructor(props: any) {
        super(props);
    }

    componentDidUpdate() {
        if (this.props.showMessage) {
            setTimeout(() => { this.props.hideMessage(); }, 100);
        }
        if (this.props.authUser !== null) {
            this.props.history.push('/');
        }
    }

    render() {
        const { showAuthLoader, userSignIn, showMessage, loader, alertMessage } = this.props;
        
        return (
            <SignInView
                showAuthLoader={showAuthLoader} userSignIn={userSignIn}
                showMessage={showMessage} loader={loader} alertMessage={alertMessage}
            />
        );
    }
}

const mapStateToProps = ({ auth }) => {
    const { loader, alertMessage, showMessage, authUser } = auth;
    return { loader, alertMessage, showMessage, authUser }
};

export default connect(mapStateToProps, {
    userSignIn, hideMessage, showAuthLoader
})(SignIn);